import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../service/user.service';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { User } from 'src/app/Model/user.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {

  isDeleted : boolean = false;
  userForm: FormGroup;
  userObject : User ;
  isSuccess : boolean  = false;
  isUpdated : boolean  = false;
  userList : Object[];
  buttonText : String = "Add";
  field: string;
  order: number;
  searchText: string;

  constructor(private userService : UserService,private router: Router) { }

  ngOnInit() {
    this.initialiseFormData();
    this.readAll();
    this.userObject = new User();
    this.order = 1;  
  }

 
 
  

  initialiseFormData(){
    this.userObject = new User();
    this.userForm = new FormGroup({
      firstName: new FormControl("",Validators.compose([ Validators.required ])),
      lastName: new FormControl("", Validators.compose([ Validators.required ])),
      employeId: new FormControl("", Validators.compose([ Validators.required ]))
   });
  }

  readAll(){
    this.userService.readAll()
            .subscribe( 
              (response : any) => {
                console.log(response);
                this.userList = response;
              },
              err => console.log('HTTP Error', err),
             () => console.log('HTTP request completed.')  

            );
  }

  resetForm(){    
    this.searchText = "";
    this.field = "";
    this.order = 1;
    this.userForm.patchValue({
      firstName: "",
      lastName: "",
      employeId: ""
    });
    this.buttonText  = "Add";
  }

  newUserFormHandler(){
    console.log(this.userForm.value);
    if (this.userForm.invalid) {
      return;
    }
    
    if(this.buttonText == "Update") {
        
        this.userService.update(this.userObject.userId ,this.userForm.value)
        .subscribe(
          (updatedUserDetail : any) => {
            console.log(updatedUserDetail);
            if(updatedUserDetail && updatedUserDetail.userId) {
                this.isUpdated = true;
                this.readAll();
                this.resetForm();
                setTimeout(()=>{
                  //this.router.navigate(['contacts'])
                  this.router.navigateByUrl('addProject')
                },3000);
            } else {
              this.isUpdated = false;
            }
        }
        );

    } else {

      this.userService.create(this.userForm.value)
      .subscribe(  (savedUserDetail : any) => {
          console.log(savedUserDetail);
          if(savedUserDetail && savedUserDetail.userId) {
              this.isSuccess = true;
              this.readAll();
              this.resetForm();
          } else {
            this.isSuccess = false;
          }
      }
        
      );
    }

    

  }

 /* populateForm(user: User) {
    this.userService.formData = Object.assign({}, user);
  } */

  editUserHandler(userId : number){
    console.log("inside edit user handler "+ userId);
      this.buttonText = "Update"
     
      this.userService.getUserById(userId).subscribe (
          
          (response : any) => {
            console.log("Edit get User Detail" + response)
            this.userObject = response;
            this.userForm.patchValue({
              firstName: this.userObject.firstName,
              lastName: this.userObject.lastName,
              employeId: this.userObject.employeId,
              userId: this.userObject.userId              
            });
            
            this.readAll();
            
            setTimeout(()=>{
              //this.router.navigate(['contacts'])
              this.router.navigateByUrl('addProject')
            },3000);

          },
          err => console.log('HTTP Error', err),
          () => console.log('HTTP request completed.')
      ); 

  }

  deleteUserHandler(id){
    this.userService.delete(id)
        .subscribe( 
          (status)=> {
              console.log(status);
              this.isDeleted = true;
              this.readAll();

              setTimeout(()=>{
                //this.router.navigate(['contacts'])
                this.router.navigateByUrl('addProject')
              },3000);
        });
  }

  sortBy(field: string) {
    this.field = field;
    this.order = this.order * (-1);
    return false;
  }

}
